import service from "../utils/service"
//封装数据请求
let link = (url, method = 'get', data, params, token) => {
    return new Promise((resolve, reject) => {
        service.request({
            url,
            method,
            data,
            params,
            headers: {'token': token},
        }).then((ok) => {
            resolve(ok)
        }).catch((err) => {
            reject(err)
        });
    })
}
export default link